body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(rgb(27, 27, 27)), to(rgb(39, 39, 39)));
  background: -webkit-linear-gradient(rgb(27, 27, 27), rgb(39, 39, 39));
  background: -o-linear-gradient(rgb(27, 27, 27), rgb(39, 39, 39));
  background: linear-gradient(rgb(27, 27, 27), rgb(39, 39, 39));
  max-width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

